@use 'utilities/variables';

* {
    box-sizing: border-box;
}

html, body {
    // overflow-x: hidden;
  }

// === SPACING ===
.spacer {
	display: block;
	width: 100%;
	&--40    { height: 40px }
	&--72    { height: 72px }
	&--80    { height: 80px }
	&--120   { height: 120px }
	&--150   { height: 150px }
    &--200   { height: 200px }
}

body {
    font-family: var(--font-family);
    font-size: 14px;
    padding: 0;
    margin: 0;
}

.container {
    width: 100%;
    max-width: 1476px;
    padding-left: 100px;
    padding-right: 100px;
    margin: 0 auto;
    &.container-sm {
        max-width: 1200px;
    }
}

ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
    transition: all .2s linear;
}

p {
    margin: 0;
    padding: 0;
}

img, svg, video  {
    max-width: 100%;
    max-height: 100%;
}

button {
    cursor: pointer;
    border: none;
    outline: none;
    background: none;
    padding: 0;
    font-family: inherit;
}

h1, h2, h3, h4, h5 {
    font-family: var(--font-family-alt);
    margin: 0;
}

h1, .h1 {
    font-size: 80px;
    font-weight: 400;
    line-height: 1.26;
}
h2, .h2 {
    font-size: 48px;
    font-weight: 500;
    line-height: 1.22;
}

h3, .h3 {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.22;
}

.label {
    padding: 15px 25px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    height: 55px;
    font-family: var(--font-family-alt);
    font-weight: 600;
    gap: 15px;
    font-size: 16px;
    &.label--primary {
        background-color: var(--color-primary);
        color: #fff;
    }
}

.subtitle {
    font-size: 18px;
    color: var(--color-gray-400);
    line-height: 32px;
}

@media screen and (max-width: 1199.99px) {
    .container {
        padding-left: 70px;
        padding-right: 70px;
    }
}

@media screen and (max-width: 1024.99px) {
    .container {
        padding-left: 40px;
        padding-right: 40px;
    }
}

@media screen and (max-width: 640.99px) {
    .container {
        padding-left: 15px;
        padding-right: 15px;
    }
}